.about {
  background-color: #000;
  padding-top: 24px;
  padding-bottom: 24px;
}

.info-section .info-container-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: reverse;
  -ms-flex-direction: column-reverse;
  flex-direction: column-reverse;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  z-index: 5;
  position: relative;
  padding: 0 8%;
}

@media (min-width: 1200px) {
  .info-section .info-container-wrapper {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
  }
}

@media screen and (min-width: 1440px) {
  .info-section .info-container-wrapper {
    padding: 0 115px;
    /* margin: -200px auto 0; */
    max-width: 1920px;
  }
}

.info-section .anime-container {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  flex-wrap: wrap;
}

.info-section .anime-container img {
  width: 100%;
  padding: 12px;
  border-radius: 100%;
}

@media screen and (max-width: 1199px) {
  .info-section .anime-container {
    width: 100%;
    max-width: 500px;
  }
}

@media (min-width: 1200px) {
  .info-section .anime-container {
    width: 50%;
    max-width: unset;
  }
}

.info-section .anime-container .dickbutts-picture {
  width: 100%;
}

.info-section .anime-container .dickbutts-img {
  width: 100%;
  height: auto;
  max-width: 800px;
  min-width: 0;
}

.info-section .text-container {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 15px;
}

@media (min-width: 576px) {
  .info-section .text-container {
    padding: 30px;
  }

  .info-section .anime-container img {
    width: calc(100% / 2);
    padding: 12px;
  }
}

@media screen and (max-width: 1199px) {
  .info-section .text-container {
    width: 100%;
    max-width: 500px;
  }
}

@media (min-width: 1200px) {
  .info-section .text-container {
    padding: 80px 50px;
    width: 50%;
  }
}

.info-section .text-container .title {
  color: #fff;
  font-family: PressStart2P;
  line-height: 1.5;
  font-weight: 400;
  font-size: 14px;
  text-align: left;
  word-break: break-word;
}

@media (min-width: 576px) {
  .info-section .text-container .title {
    font-size: 18px;
  }
}

@media (min-width: 1200px) {
  .info-section .text-container .title {
    font-size: 22px;
  }
}

.info-section .text-container .text {
  color: #fff;
  font-family: PressStart2P;
  line-height: 1.5;
  font-weight: 400;
  font-size: 12px;
  text-align: left;
  word-break: break-word;
}

@media (min-width: 576px) {
  .info-section .text-container .text {
    font-size: 14px;
  }
}

@media (min-width: 1200px) {
  .info-section .text-container .text {
    font-size: 16px;
  }
}
